/**
 * Utils / Functions / Rem
 *
 * Rem utility for converting pixel values to a relative 'em'.
 */
@function rem($pixels, $context: 16px) {

    @if (unitless($pixels)) {
        $pixels: $pixels * 1px;
    }

    @if (unitless($context)) {
        $context: $context * 1px;
    }

    @return $pixels / $context * 1rem;

}
